@use 'sass:map';

.wp-block-site-functionality-tout-linked {
	position: relative;
	display: block;
	width: 100%;
	overflow: hidden;
	aspect-ratio: 3/2;


	.title__wrapper {
		position: absolute;
		top: 0;
		left: 0;
		padding: get-spacing(0.5);
		background-color: rgba(#000, 0.5);
		border: 2px color('white') solid;
		margin: get-spacing(0.5);
		width: calc( 100% - get-spacing());
		height: calc( 100% - get-spacing());
		transition-property: background-color;
		transition-timing-function: ease-out;
		transition-duration: 0.25s;

		.tout__title {
			position: absolute;
			bottom: get-spacing(0.5);
			color: color('white');
			@include acumin;
			font-size: map.get($font-sizes, h3);
			margin-bottom: 0;
			transition-property: box-shadow;
			box-shadow: 0 0 0 rgba( #000, 0);
			transition-timing-function: ease-out;
			transition-duration: 0.25s;
		}
	}

	&:hover,
	&:focus {
		.title__wrapper {
			background-color: rgba(#000, 0);
		}
		.tout__title {
			text-decoration: underline;
			text-shadow: 4px 4px 7px rgba( #000, 0.85);
		}
	}

	.tout__image {
		width: 100%;
		img {
			width: 100%;
		}
	}

	svg {
		width: 1rem;
		height: 1rem;
	}
}
